<script>
  export let examples = [];

  let selectedIndex = 0;

  function selectExample(index) {
    selectedIndex = index;
  }

  $: {
    selectedIndex = 0;
  }
</script>

<style type="text/scss">.example {
  font-family: 'Overlock', cursive;
  font-size: 1.2rem;
  position: relative;
  background: #fff;
  border: 1px solid #ccc;
  padding: 0.5rem;
  border-radius: 0.5rem; }
  .example:after, .example:before {
    top: 100%;
    left: 50%;
    border: solid transparent;
    content: ' ';
    height: 0;
    width: 0;
    position: absolute;
    pointer-events: none; }
  .example:after {
    border-color: transparent;
    border-top-color: #fff;
    border-width: 1rem;
    margin-left: -1rem; }
  .example:before {
    border-color: transparent;
    border-top-color: #ccc;
    border-width: calc(1rem + 1px);
    margin-left: calc(-1rem - 1px); }

.example-button {
  inline-size: 3rem;
  block-size: 3rem;
  border-radius: 50%;
  font-size: 1.3rem;
  text-align: center;
  background-color: #fff;
  border: 1px solid #ccc;
  margin: 0.5rem;
  cursor: pointer; }
  .example-button.selected {
    color: white;
    background-color: #44a;
    border: none; }
  .example-button:focus {
    outline: none; }
</style>

<div>
  <p class="example">{examples[selectedIndex]}</p>
  {#each examples as e, i}<button class="example-button" class:selected={selectedIndex === i} on:click={() => selectExample(i)} />{/each}
</div>
